<template>

<div id="formio-land-form">
        <h1>{{ formName }} <small><cite>(Land Act 1994)</cite></small></h1>
                <FormWrapper :projectUrl="projectUrl" :wizardUrl="wizardUrl" :createFormUrl="createFormUrl"
                    :formIdentifier="formIdentifier" :savedApplicationPage="savedApplicationPage"
                    :thankYouPage="thankYouPage" :submissionId="submissionId" :hasPayment="hasPayment"
                    :paymentUrl="paymentUrl" :recaptchaSiteKey="recaptchaSiteKey" :purchaseItemName="purchaseItemName"
                    :guidePageUrl="guidePageUrl" :shortUuidToken="shortUuidToken" 
                    :feesCode="feesCode" :feesUrl="feesUrl" :privateKey="privateKey"
                    :authUrl="authUrl" :formInfo="formInfo"/>
        <div class="clearfix"></div>
</div>

</template>

<script>
import { FormWrapper } from 'formio-wizard-vite-wrapper';

export default {
    name: 'App',
    components: {
        FormWrapper
    },
    data() {

        return {
            // FormIO
            projectUrl: window.formDetails.VUE_APP_PROJECT_URL,
            wizardUrl: `${window.formDetails.VUE_APP_PROJECT_URL}/${window.formDetails.VUE_APP_FORM_PATH}`,
            createFormUrl: window.formDetails.VUE_APP_WIZARD_URL,
            formIdentifier: `${window.formDetails.VUE_APP_NAME}-${window.formDetails.VUE_APP_ENV}`,
            formName: `${window.formDetails.VUE_APP_FORM_NAME}`,
            purchaseItemName: window.formDetails.VUE_APP_PURCHASE_ITEM_NAME,

            // Matrix page
            savedApplicationPage: window.formDetails.VUE_APP_SAVED_APPLICATION_PAGE,
            thankYouPage: window.formDetails.VUE_APP_THANK_YOU_PAGE,
            guidePageUrl: window.formDetails.VUE_APP_GUIDE_PAGE_URL,

            // submission
            submissionId: '',

            // payment
            hasPayment: window.formDetails.VUE_APP_HAS_PAYMENT,
            paymentUrl: window.formDetails.VUE_APP_PAYMENT_URL,
            recaptchaSiteKey: window.formDetails.VUE_APP_RECAPTCHA_SITE_KEY,

            // temp user authentication
            shortUuidToken: '',
            feesCode: window.formDetails.VUE_APP_FEECODE,
            feesUrl: window.formDetails.VUE_APP_FEE_URL,
            privateKey: window.formDetails.VUE_APP_PRIVATE_KEY,
            authUrl: window.formDetails.VUE_APP_AUTH_URL,
            formInfo:window.formDetails.VUE_APP_FORM_INFO
        }
},
    beforeMount() {
        let queryString = window.location.search;
        let urlParams = new URLSearchParams(queryString);
        let submissionId = urlParams.get('submissionId') ?? ''
        let shortUuidToken = urlParams.get('token') ?? ''
        this.createFormUrl = this.wizardUrl
        if (submissionId !== '' && shortUuidToken !== '') {
            this.createFormUrl += '/submission/' + submissionId
        }

        this.submissionId = submissionId
        this.shortUuidToken = shortUuidToken
    },
}
</script>